button.word {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 44px;
    min-height: 44px;
    max-height: 44px;
    margin: 2px;
    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 4px;
    padding: 0 8px;
    background-color: var(--thm-bg3);
    transition: all .3s;
    color: #000;
    cursor: pointer;
    /*
    opacity: 0;
    transition: opacity .6s;
    */
}

button.word:active {
    background-color: var(--thm-bgSL);
    transition: all .3s;
}

/*
button.word.show {
    opacity: 1;
    transition: opacity .6s;
}
*/
button.word>div.types {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 40px;
}

button.word>div.name {
    font-weight: bold;
    font-size: 120%;
}

button.word>div.types>div {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    width: 10px;
    height: 10px;
    margin: 1px;
    font-size: 8px;
    font-weight: bold;
    color: #000;
    background: #aaa;
}

button.word>div.types>div.grey {
    opacity: .4
}

button.word>div.types>div.noun {
    background: #77f
}

button.word>div.types>div.adjective {
    background: #ff7
}

button.word>div.types>div.verb {
    background: #f77
}

button.word>div.types>div.conjunction {
    background: #f80
}

button.word>div.types>div.preposition {
    background: #f7f
}

button.word>div.types>div.adverb {
    background: #7f7
}